@import url(https://fonts.googleapis.com/css2?family=Cabin&family=Inconsolata&family=Nunito&family=Nunito+Sans&family=Pacifico&family=Quicksand&family=Roboto+Mono:wght@500&family=Rubik&family=VT323&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.table {
    margin-top: 20px;
    /* padding: 20px; */
    overflow: scroll;
    max-height: 350px;
    text-align: center;   
}

.table tr {
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: 0.5rem;
}

.table tr:nth-of-type(odd) {
    color: rgb(3, 3, 3);
    background-color: rgb(255, 194, 194);
    font-weight: 900;

}
.map {
    height: 500px;
    background: white;
    padding:1rem;
    width: 100%;
    margin-top: 1em;
    -webkit-filter: drop-shadow(1em 0.75em);
            filter: drop-shadow(1em 0.75em);
}

.map .leaflet-container {
    height: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ffa2a2;
  font-family: "Roboto Mono", monospace;
}

.app__left {
  padding: 50px;
  margin: 0;
}

.app__right {
  text-align: center;
  margin: 50px;
  -webkit-filter: drop-shadow(1em 0.75em);
          filter: drop-shadow(1em 0.75em);
}

.app {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  width: 75vw;
}

.app__header {
  font-family: "Quicksand", sans-serif;
  border-bottom: 3px dashed black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
}

.app__stats {
  font-family: "Inconsolata", monospace;
  font-size: 20px;
  justify-content: space-between;
  display: flex;
  -webkit-filter: drop-shadow(0.75em 0.75em);
          filter: drop-shadow(0.75em 0.75em);
}

.map {
  display: inline-block;
  margin-top: 50px;
}

@media (max-width: 900px) {
  .app {
    margin: 50px;
    flex-direction: column;
  }
}

